:root {
  --primary: #313131;
  --seconday: #242076;
  --light: #d4d4d4;
  background-color: rgb(126, 121, 142);
}

* {
  margin: 0;
  padding: 0;
  font-family: monospace;
  font-size: 30px;
  box-sizing: border-box;
}

.App {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
}

.Calculator {
  overflow: hidden;
  border-radius: 20px;
  width: 360px;
  height: 400px;
  background-color: var(--primary);
}

.Display {
  padding: 10px;
  height: 16.66%;
  background-color: var(--light);
  text-align: right;
  font-size: 1.3em;
  
}

.Display span {

}

.Operators {
  height: 16.66%;
  background-color: var(--seconday);
  width: 100%;
}

.Operators > * {
  width: 20%;
  height: 100%;
}

.Digits {
  height: 66.66%;
}

.Digits * {
  width: 33.33%;
  height: 25%;
}

button {
  border: none;
  background: none;
  color: var(--light);
}

button:hover {
  background-color: rgb(123, 14, 14);
}